.banner2{
max-width: 80vw;
min-height: 140px;
padding: 0 2rem 0;
background-image:url("../source2-image.png");
background-size: cover;
background-position: center;
margin: auto;
border-radius: 25px;
display: grid;
place-items: center;
color: #ffffff;
font-weight: 700;
position: relative;
box-shadow: 0px 4px 4px 0px #00000040;
z-index: 2;
}
.banner::after{
position: absolute;
content: "";
z-index: -1;
opacity: 0.5;  
border-radius: 25px;
inset: 0;
background-color:rgba(0, 0, 0, 0.697) ;
}
.boxDeServices{
display: flex;
flex-direction: column;
gap: 15px;
margin: 10px auto;
}
.box{
background-color: #ff6060;
width: 80%;
margin: 15px auto;
display: flex;
justify-content: space-between;
align-items: center;
border-radius: 5px;
padding:10px, 20px, 10px, 15px;
height: 52px;
}
.subTitleServices {

color: #ffffff;

font-size: 24px;
font-weight: 700;
padding: 0 0 0 5px;
}
.iconDES{
font-size: 18px;
font-weight: 700;
}
@media (max-width:776px){
.banner2{
min-height: 100px;
padding: 0 5px 0;
}
.box{
    height: 30px;
    padding: 0;
    margin: 0 auto;
}
.subTitleServices{
font-size: 13px;
padding: 5px;
margin: 5px auto;
font-weight: 700;
}
}