.zone-description {
    max-width: 100%;
    margin: 10px auto;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.zone1, .zone2 {
    display: flex;
    flex-direction: column;
}

.zone1 {
    margin-right: 20px;
}

.zone2 {
    margin-left: 20px;
}

.tit1 {
    display: flex;
    justify-content: space-between;
    background-color: #ff6060;
    border-radius: 15px;
    align-items: center;
}

.paragraph_description1 {
    background-color: #dedddda5;
    color: #ff6060;
    border-radius: 15px;
    text-align: start;
}

.description_title1 {
    font-size: 18px;
    color: white;
    padding: 0 0 0 5px;

}

.iconDES {
    color: white;
    font-size: 18px;
    align-items: center;
    margin-right: 10px;
}
@media (max-width: 776px){
    .zone-description{
        max-width: 90%;
    }
    .zone1 {
        margin-right: 0;
    }
    
    .zone2 {
        margin-left: 0;
    }
}